export class Random {
    public static getRandom(): number {
        return Math.random();
    }

    public static getRandomReal(min: number, max: number): number {
        return Math.random() * (max - min) + min;
    }

    public static getRandomInt(min: number, max: number): number {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min)) + min; //The maximum is exclusive and the minimum is inclusive
    }

    public static getRandomIntInclusive(min: number, max: number): number {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min; //The maximum is inclusive and the minimum is inclusive 
    }

    public static getRandomBool(): boolean {
        return !!Random.getRandomIntInclusive(0, 1);
    }    
}